<template>
  <v-row class="fill-height">
    <v-col align="center">
      <v-sheet height="64">
        <v-toolbar flat color="white">
          <v-btn outlined class="mr-4" color="grey darken-2" @click="setToday">
            Today
          </v-btn>
          <v-btn fab text small color="grey darken-2" @click="prev">
            <v-icon small>mdi-chevron-left</v-icon>
          </v-btn>
          <v-btn fab text small color="grey darken-2" @click="next">
            <v-icon small>mdi-chevron-right</v-icon>
          </v-btn>
          <v-spacer></v-spacer>
          <v-menu bottom right>
            <template v-slot:activator="{ on }">
              <v-btn outlined color="grey darken-2" v-on="on" @click="test1">
                <span>{{ title }}</span>
              </v-btn>
            </template>
            <v-list>
              <v-list-item @click="selectYear = 2010" @change="test2">
                <v-list-item-title>2010</v-list-item-title>
              </v-list-item>
              <v-list-item @click="selectYear = 2020"  @change="test2">
                <v-list-item-title>2020</v-list-item-title>
              </v-list-item>
              <v-list-item @click="selectYear = 2021"  @change="test2">
                <v-list-item-title>2021</v-list-item-title>
              </v-list-item>
              <v-list-item @click="selectYear = 2022" @change="test2">
                <v-list-item-title>2022</v-list-item-title>
              </v-list-item>
            </v-list>
          </v-menu>
        </v-toolbar>
      </v-sheet>
      <v-sheet height="600">
        <v-calendar
          ref="calendar"
          v-model="focus"
          color="primary"
          :events="events"
          :event-color="getEventColor"
          :now="today"
          :type="type"
          @click:event="showEvent"
          @click:more="viewDay"
          @change="updateRange"
        ></v-calendar>
        <v-dialog v-model="event_dialog" width="600px">
          <v-card>
            <v-card-title>
              <span class="headline">Use Google's location service?</span>
            </v-card-title>
            <v-card-text>{{ eventContent }}</v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="green darken-1" text @click="event_dialog = false"></v-btn>
              <v-btn color="green darken-1" text @click="event_dialog = false"></v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-sheet>
    </v-col>
  </v-row>
</template>

<script>
  export default {
    data: () => ({
      focus: '', //  
      type: 'month', //   
      start: null,
      end: null,
      selectedEvent: {},
      selectedElement: null,
      selectedOpen: false,
      events: [],
      colors: ['blue', 'indigo', 'deep-purple', 'cyan', 'green', 'orange', 'grey darken-1'],
      names: ['1', '2', '3', '4', '5', '6', '7', '8'],
      today: null, //      
      selectYear: null, 
      // eventContent: '',//    .
      event_dialog: false,// 
      selectEventList: [],
    }),
    computed: {
      title () {
        const { start, end } = this
        if (!start || !end) {
          return ''
        }
        const startMonth = this.monthFormatter(start)
        // console.log(startMonth); //title  
        const endMonth = this.monthFormatter(end)
        // console.log(endMonth); //title   
        const suffixMonth = startMonth === endMonth ? '' : endMonth
        // console.log(suffixMonth); //title     ,        

        const startYear = start.year
        // console.log(startYear);
        const endYear = end.year
        // console.log(endYear);
        const suffixYear = startYear === endYear ? '' : endYear
        // console.log(suffixYear);

        const startDay = start.day + this.nth(start.day)
        // console.log(startDay); //title  
        const endDay = end.day + this.nth(end.day)
        // console.log(endDay); //title  

        //type     title   
        // switch (this.type) {
        //   case 'month':
        //     return  startYear+" " + startMonth
        //   case 'week':
        //   case '4day':
        //     return `${startMonth} ${startDay} ${startYear} - ${suffixMonth} ${endDay} ${suffixYear}`
        //   case 'day':
        //     return `${startMonth} ${startDay} ${startYear}`
        // }
        
        //type   switch  
        return startYear+" " + startMonth
      },
      monthFormatter () {
        return this.$refs.calendar.getFormatter({
          timeZone: 'UTC', month: 'long',
        })
      },
      eventContent ( ) {
        // this.selectedEvent = event
        // this.selectedElement = nativeEvent.target

        // return this.selectedEvent + " / " +  this.selectedElement
        return "asdsad"
      },
    },
    mounted () {
      this.$refs.calendar.checkChange()
      // console.log(this.$vuetify.breakpoint)
    },
    methods: {
      test1 () {
        // date  
        //  Date()    ,   
        const date1 = new Date('12 17, 1995 03:24:00');
      },
      test2 () {
        this.$refs.calendar.move((this.selectYear - this.start.year) * 12) 
          
      },
      getEventColor (event) {
        return event.color
      },
      //Today  
      //this.focus   .
      setToday () {
        this.focus = this.today
      },
      //title    
      prev () {
        //calendar  .
        this.$refs.calendar.prev()
      },
      //title    
      next () {
        //calendar  .
        this.$refs.calendar.next()
      },
      //   
      showEvent ({ nativeEvent, event }) {
        this.selectedEvent = event
        this.selectedElement = nativeEvent.target
        console.log(this.selectedEvent)
        console.log(this.selectedElement)
        this.event_dialog = true
      },
      //more 
      viewDay (event){
        // console.log(this.events)
        // console.log(event.date)
        // console.log(this.events)//     

        const selectDate = event.date
        const eventList = this.events
        var selectDateEvents = []
        var number = 0;

        console.log(eventList)
        alert(selectDate)
        alert(eventList[0].start)
        for(let i = 0; i< eventList.length; i++){
          // if(eventList[i].start.IndexOf(selectDate)){
          //   number ++;
          //   console.log(eventList[i].start)
          // }
          console.log(eventList[i].start.indexOf(selectDate))
        }
        // 2020-01-28  mm 
        // 2020-1-24 0:45  h
        alert(number)
      },
      //  .
      // start, end data start end  
      // start, end calendar start end
      updateRange ({ start, end }) {
        //    
        // start end    db     
        //db  name, start, end,  , url 
        const events = []
        const min = new Date(`${start.date}T00:00:00`)
        const max = new Date(`${end.date}T23:59:59`)
        const days = (max.getTime() - min.getTime()) / 86400000
        const eventCount = this.rnd(days, days + 20)
        
        for (let i = 0; i < eventCount; i++) {
          const allDay = this.rnd(0, 3) === 0
          const firstTimestamp = this.rnd(min.getTime(), max.getTime())
          const first = new Date(firstTimestamp - (firstTimestamp % 900000))
          const secondTimestamp = this.rnd(2, allDay ? 288 : 8) * 900000
          const second = new Date(first.getTime() + secondTimestamp)

          events.push({
            name: this.names[this.rnd(0, this.names.length - 1)],
            start: this.formatDate(first, !allDay),
            end: this.formatDate(second, !allDay),
            color: this.colors[this.rnd(0, this.colors.length - 1)],
          })
        }

        this.start = start
        this.end = end
        this.events = events 
      },
      // 
      nth (d) {
        return d > 3 && d < 21
          ? 'th'
          : ['th', 'st', 'nd', 'rd', 'th', 'th', 'th', 'th', 'th', 'th'][d % 10]
      },
      //      db db  
      rnd (a, b) {
        return Math.floor((b - a + 1) * Math.random()) + a
      },
      //a Date(), withTime 
      //     db   
      formatDate (a, withTime) {
        return withTime
          ? `${a.getFullYear()}-${a.getMonth() + 1}-${a.getDate()} ${a.getHours()}:${a.getMinutes()}`
          : `${a.getFullYear()}-${a.getMonth() + 1}-${a.getDate()}`
      },
    },
  }
</script>